@font-face {
  font-family: 'coolpic';
  src:
    url('fonts/coolpic.ttf?544w19') format('truetype'),
    url('fonts/coolpic.woff?544w19') format('woff'),
    url('fonts/coolpic.svg?544w19#coolpic') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

.icon {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'coolpic' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

